import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddRndAbout from "./AddRndAbout";
import { useTranslation } from "react-i18next";
import RndAboutCard from "./RndAboutCard";
import Loading from "../../../Loader/Loading";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const RndAbout = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //
  // console.info("sdjkhgfvsdj", iqacDashboardMaster?.about);
  return (
    <>
      <div
        className={style.fm_header_container}
        style={{
          marginTop: "1rem",
        }}
      >
        <h6></h6>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>
      <BorderBottom />

      {iqacDashboardMaster?.about?.map((sub) => (
        <RndAboutCard sub={sub} key={sub?._id} />
      ))}
      {iqacDashboardMasterLoading && <Loading />}

      {add && (
        <AddRndAbout
          onClose={onToggle}
          onRefetch={iqacDashboardMasterRefetch}
          qcid={qcid}
        />
      )}
    </>
  );
};

export default RndAbout;
